import { PropsWithChildren } from 'react';

import Typography from 'components/ui/Typography';

import classNames from 'classnames';

// ----------------------------------------------------------------------

interface ContentSectionProps extends PropsWithChildren {
  gap?: 4 | 8 | 12 | 16 | 20 | 24;
}

const SECTION_GAP_MAPS = {
  4: 'gap-y-1',
  8: 'gap-y-2',
  12: 'gap-y-3',
  16: 'gap-y-4',
  20: 'gap-y-5',
  24: 'gap-y-6'
};

const ContentSection = ({ gap = 8, children }: ContentSectionProps) => {
  return <div className={classNames('flex flex-col', SECTION_GAP_MAPS[gap])}>{children}</div>;
};

// ----------------------------------------------------------------------

interface ContentSectionTitleProps extends PropsWithChildren {
  variant?: 'head' | 'body';
  size?: 'xs' | 'sm' | 'base' | 'lg' | 'xl';
  weight?: 400 | 500 | 700;
  color?: string;
}

const ContentSectionTitle = ({
  variant = 'head',
  size = 'sm',
  weight = 700,
  color = 'text-gray-800',
  children
}: ContentSectionTitleProps) => {
  return (
    <Typography variant={variant} size={size} weight={weight} color={color}>
      {children}
    </Typography>
  );
};

// ----------------------------------------------------------------------

interface ContentProps extends PropsWithChildren {
  gap?: 20 | 30 | 40 | 50 | 60 | 70;
  pt?: string;
  pb?: string;
  px?: string;
  backgroundColor?: string;
}

const GAP_MAPS = {
  20: 'gap-y-5',
  30: 'gap-y-[1.875rem]',
  40: 'gap-y-10',
  50: 'gap-y-[3.125rem]',
  60: 'gap-y-[3.75rem]',
  70: 'gap-y-[4.375rem]'
};

const Content = ({
  gap = 70,
  pt = 'pt-[3.75rem]',
  pb = 'pb-[4.375rem]',
  px = 'px-10',
  backgroundColor = 'bg-white',
  children
}: ContentProps) => {
  return (
    <div className={classNames('flex flex-col', GAP_MAPS[gap], pt, pb, px, backgroundColor)}>
      {children}
    </div>
  );
};

Content.Section = ContentSection;
Content.SectionTitle = ContentSectionTitle;

export default Content;
